import React from "react";
import styled from "styled-components";

//const ParralaxContainer = styled.div``;

const layerCss = `
position: absolute;
top: 0;
right: 0;
bottom: 0;
left: 0;
`;

const ParallaxBackLayer = styled.div`
  ${layerCss};
  transform: translateZ(-1);
`;

const ParallaxBaseLayer = styled.div`
  ${layerCss};
  transform: translateZ(0);
`;

const Parallax = ({ children }) => (
  <div style={{ height: "100px" }}>
    <ParallaxBackLayer>Back Layer</ParallaxBackLayer>
    <ParallaxBaseLayer>{children}</ParallaxBaseLayer>
  </div>
);
export default Parallax;



